import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Home from './Home';
import GraphicsIndex from './graphics/GraphicsIndex';
import MarkerMachine from './markers/MarkerMachine';
import Nle from './nle/Nle';
import Header from './Header';
import TheGrid from './grid/TheGrid';
import NotFound from './NotFound';

const App = () => (
  <BrowserRouter>
    <main className="grid-master">
      <Header className="header"/>
      <div className="main-content">
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/graphics" component={GraphicsIndex} />
        <Route exact path="/markermachine" component={MarkerMachine} />
        <Route exact path="/nle" component={Nle} />
        <Route exact path="/thegrid" component={TheGrid} />
        <Route component={NotFound} />
      </Switch>
      </div>
    </main>
  </BrowserRouter>
);

export default App;



