import React from 'react';
import PropTypes from 'prop-types';
import logo from '../images/logo.svg';
import { NavLink } from 'react-router-dom';


const HeaderLink = ({ children, ...props }) => (
  <NavLink
    exact
    className="p1 mx1 right black rounded text-decoration-none"
    activeClassName="bg-white"
    {...props}
  >
    {children}
  </NavLink>
);

const Header = (props) => (
  <header className="header-nav">
    <h1 className="h1 roboto-900 header-title">
      react video machine
    </h1>
    <nav className="header-links roboto-900">
      <HeaderLink to="/markermachine">
        markers
      </HeaderLink>
      <HeaderLink to="/nle">
        nle
      </HeaderLink>
      <HeaderLink to="/graphics">
        graphics
      </HeaderLink>
      <HeaderLink to="/thegrid">
        the grid
      </HeaderLink>
      <HeaderLink to="/">
        home
      </HeaderLink>
    </nav>
  </header>
);

export default Header;



